body{
    background-color: rgb(19, 63, 63);
}

.container1 {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    width: 85%;
    background-color: rgb(141, 247, 210);
    border: 2rem solid rgba(206, 71, 230, 0.421); /* Added border property for outlining */
    border-radius: 15px; /* Curved borders on all four edges */
  }
  
  
  .container2 {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    width: 80%;
    background-color: rgb(141, 183, 247);
    border: 2rem solid rgb(172, 226, 247); /* Added border property for outlining */
    border-radius: 15px; /* Curved borders on all four edges */
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Adjust minmax value */
    gap: 20px;
  }
  
  /* Styles for cards in container1 */
  .container1 .card {
    border: 4px solid #0ac40a;
    border-radius: 5px;
    overflow: hidden;
    border-radius: 15px;
    background-color: rgb(69, 56, 29); /* Change the background color */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.484);
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  
  .container1 .card:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.66);
    transform: translateY(-3px);
  }
  
  /* Styles for cards in container2 */
  .container2 .card {
    border: 4px solid #261f3f;
    border-radius: 5px;
    overflow: hidden;
    border-radius: 15px;
    background-color: rgb(61, 60, 20); /* Change the background color */
    box-shadow: 0 4px 8px rgb(0, 0, 0);
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  
  .container2 .card:hover {
    box-shadow: 0 6px 12px rgb(0, 0, 0, 0.66);
    transform: translateY(-3px);
  }
  
  .card-body {
    padding: 20px;
  }
  
  .card-title {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .card-text {
    font-size: 14px;
    margin-bottom: 5px;
  }
  